import Image from "next/image";

export default async function ProfilePage({ searchParams } : { searchParams: { [key: string]: string }}) {
    const result = await (searchParams.data ? JSON.parse(decodeURIComponent(searchParams.data)) : null);
    // result.accountData.account.blabla
  return (
    <div>
      <Image src={result.accountData.account.avatar} alt="Avatar" width={50} height={50}/>
    </div>
  )
}
